import React from "react";
import Title from "../../components/Title";
import Info from "../../components/Info";
import { Wrapper, Split } from "./styled";
import { chips } from "../../data";
import ButtonClose from "../../components/ButtonClose";
import { Link } from "react-router-dom";
import ButtonArrowLeft from "../../components/ButtonArrowLeft";
import ButtonArrowRight from "../../components/ButtonArrowRight";
const Item = ({ match }) => {
  const params = match.params.itemId;
  const actualChip = chips.filter(chip => {
    return chip.first.id === Number(params);
  });
  const actualReversedChip = chips.filter(
    chip => chip.second.id === Number(params)
  );
  return (
    <div>
      {(actualChip.length > 0 && actualChip[0].first.id === 7) ||
      (actualReversedChip.length > 0 &&
        actualReversedChip[0].second.id === 6) ? (
        <Link to="/menu">
          <ButtonArrowLeft>
            <i
              className={
                actualReversedChip.length > 0 &&
                (actualReversedChip[0].second.id === 6 ||
                  actualReversedChip[0].second.id === 8 ||
                  actualReversedChip[0].second.id === 10)
                  ? "fas fa-chevron-circle-left green"
                  : "fas fa-chevron-circle-left"
              }
            />
          </ButtonArrowLeft>
        </Link>
      ) : null}

      <Wrapper>
        <Link to="/menu">
          <ButtonClose>
            <i
              className={
                actualReversedChip.length > 0 &&
                (actualReversedChip[0].second.id === 6 ||
                  actualReversedChip[0].second.id === 8 ||
                  actualReversedChip[0].second.id === 10)
                  ? "far fa-times-circle green"
                  : "far fa-times-circle"
              }
            />
          </ButtonClose>
        </Link>
        <Split>
          <Title />
          <Info
            chip={
              actualChip.length > 0
                ? actualChip[0].first
                : actualReversedChip[0].second
            }
          />
        </Split>
        <Split>
          <img
            src={
              actualChip.length > 0
                ? actualChip[0].first.image1
                : actualReversedChip[0].second.image1
            }
            alt="image1"
          />
        </Split>
        <Split>
          <img
            src={
              actualChip.length > 0
                ? actualChip[0].first.image2
                : actualReversedChip[0].second.image2
            }
            alt="image2"
          />
        </Split>
      </Wrapper>
      {(actualChip.length > 0 && actualChip[0].first.id === 7) ||
      (actualReversedChip.length > 0 &&
        actualReversedChip[0].second.id === 6) ? (
        <Link to="/menu">
          <ButtonArrowRight>
            <i
              className={
                actualReversedChip.length > 0 &&
                (actualReversedChip[0].second.id === 6 ||
                  actualReversedChip[0].second.id === 8 ||
                  actualReversedChip[0].second.id === 10)
                  ? "fas fa-chevron-circle-right green"
                  : "fas fa-chevron-circle-right"
              }
            />
          </ButtonArrowRight>
        </Link>
      ) : null}
    </div>
  );
};

export default Item;
