import styled from "styled-components";
export const SubTitle = styled.h3`
  font-weight: 400;
  font-size: 16px;
  color: #0074d9;
`;
export const Title = styled.h1`
  font-weight: 400;
  font-size: 24px;
`;
export const Wrapper = styled.div`
  display: flex;
  flex-direction: column;
  margin-top: -3rem;
  > * {
    margin-left: 3rem;
    margin-right: 2rem;
  }
  hr {
    border: 1px solid #cbcbcb;
    width: 80%;
  }
  .green {
    color: green;
  }
`;
